import React from 'react';
import {Nav, Navbar, Alert} from 'react-bootstrap';
import styled from 'styled-components';
import {Login} from '../components/Login/index';
import { faHome } from "@fortawesome/free-solid-svg-icons";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {Register} from '../components/Login/index';


import Modal from 'react-modal';


const Styles = styled.div`
    .navbar{
        background-color: #222;
    }

    .navbar-brand, .navbar-nav .nav-link {
        color:#bbb;


        &:hover{
            color: white;
        }
    }
`;

export function NavigationBar(){

    // ******* function modales

    const [modalIsOpenLogin,setIsOpenLogin] = React.useState(false);
    const [modalIsOpenRegister,setIsOpenRegister] = React.useState(false);

    function handleSelect(selectedKey) {
        if(selectedKey === "1")
        {
            setIsOpenLogin(true);
        }
        else if (selectedKey === "2")
        {
            setIsOpenRegister(true);
        }
      }  

    const customStylesLogin = {
    content : {
        width                 : '30%',
        height                : '65%',
        top                   : '50%',
        left                  : '50%',
        right                 : 'auto',
        bottom                : 'auto',
        marginRight           : '-50%',
        transform             : 'translate(-50%, -50%)'
    }
    };

    const customStylesRegister = {
        content : {
            width                 : '30%',
            height                : '80%',
            top                   : '50%',
            left                  : '50%',
            right                 : 'auto',
            bottom                : 'auto',
            marginRight           : '-50%',
            transform             : 'translate(-50%, -50%)'
        }
        };

    var subtitle;
    function openModalRegister() {
        setIsOpenRegister(true);
    }

    function afterOpenModalRegister() {
        // references are now sync'd and can be accessed.
        subtitle.style.color = '#f00';
    }

    function closeModalRegister(){
        setIsOpenRegister(false);
    }


    var subtitle;
    function openModalLogin() {
        setIsOpenLogin(true);
      }
    
      function afterOpenModalLogin() {
        // references are now sync'd and can be accessed.
        subtitle.style.color = '#f00';
      }
    
      function closeModalLogin(){
        setIsOpenLogin(false);
      }
      // function modales *******

      //****** Main return
    return(
        <Styles>
        <Navbar expand="lg">
            <Navbar.Brand href="/">CodeLife</Navbar.Brand>
            <Navbar.Toggle/>
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="ml-auto" onSelect={handleSelect}>
                    <Nav.Item><Nav.Link href="/">Home <FontAwesomeIcon icon={faHome} /></Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link eventKey={1} title="Login">Login </Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link eventKey={2} title="Register">Register</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link href="/About">About</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link href="/Contact">Contact</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link href="/Stats">Stats</Nav.Link></Nav.Item>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
        <Modal
              isOpen={modalIsOpenLogin}
              onAfterOpen={afterOpenModalLogin}
              onRequestClose={closeModalLogin}
              contentLabel="Login modal"
              style={customStylesLogin}
            >
    
              <h2 ref={_subtitle => (subtitle = _subtitle)}></h2>
              <FontAwesomeIcon icon={faTimes} color="red" style={{cursor: "pointer" }} size="2x" onClick={closeModalLogin}/>
              <form>
                  <Login/>
              </form>
            </Modal>

        <Modal
            isOpen={modalIsOpenRegister}
            onAfterOpen={afterOpenModalRegister}
            onRequestClose={closeModalRegister}
            contentLabel="Register Modal"
            style={customStylesRegister}
        >

            <h2 ref={_subtitle => (subtitle = _subtitle)}></h2>
            <FontAwesomeIcon icon={faTimes} color="red" style={{cursor: "pointer" }} size="2x" onClick={closeModalRegister}/>
            <form>
                <Register/>
            </form>
        </Modal>
    </Styles>

    )
}


