import loginImg from "../../assets/Welcome.svg";
import "./style.scss";
import React, { useState } from 'react';
import { Container } from 'react-bootstrap';

export function Register(){

    //username state
    const [username,setUsername] = useState("1");
    const [shadowUser,setShadowUser] = useState("transparent");

    //password state
    const [password,setPassword] = useState("1");
    const [shadowPassword,setShadowPassword] = useState("transparent");

    //email state
    const [email,setEmail] = useState("1");
    const [shadowEmail,setShadowEmail] = useState("transparent");

    //phone state
    const [phone,setPhone] = useState("1");
    const [shadowPhone,setShadowPhone] = useState("transparent");


    function usernameValid(value){

        setUsername(value)

        if(value == ""){
            setShadowUser("red");
            return false;
        }
        else if(value.length  < 4) {
            setShadowUser("red");
            return false;
        }
        else{
            setShadowUser("#0e81ce96");
            return true;
        }
    }

    function passwordValid(value){

        setPassword(value)

        if(value == ""){
            setShadowPassword("red");
            return false;
        }
        else if(value.length  < 4) {
            setShadowPassword("red");
            return false;
        }
        else{
            setShadowPassword("#0e81ce96");
            return true;
        }
    }

    function emailValid(value){

        setEmail(value)

        if(value == ""){
            setShadowEmail("red");
            return false;
        }
        else if(!!!(value.includes("@"))){
            setShadowEmail("red");
            return false;
        }
        else if (!!!(value.includes("."))){
            setShadowEmail("red");
            return false;
        }
        else if(value.length  < 4) {
            setShadowEmail("red");
            return false;
        }
        else{
            setShadowEmail("#0e81ce96");
            return true;
        }
    }

    function phoneValid(value){

        setPhone(value)

        if(value == ""){
            setShadowPhone("red");
            return false;
        }
        else if(!!!(value.length  === 10)) {
            setShadowPhone("red");
            return false;
        }
        else{
            setShadowPhone("#0e81ce96");
            return true;
        }
    }



    function beforeValid(){
        var result;

        result = usernameValid(username);
        result = passwordValid(password) && result;
        result = emailValid(email) && result;
        result = phoneValid(phone) && result;


        if(result === true){

        }

        return result;
    }


        return(
            <div className="base-container">
                <div className="header">Register</div>
                <div className="content">
                    <div className="image">
                        <img src={loginImg}/>
                    </div>
                    <div className="form">
                        <div className="form-group">
                            <label htmlFor="username">Username</label>
                            <br/>
                            <input type="text" name="username" style={{boxShadow: "0px 0px 12px 0.8px " + shadowUser}} placeholder="username" onChange={e => usernameValid(e.target.value)}/>
                        </div>
                        <div className="form-group">
                            <label htmlFor="password">Password</label>
                            <br/>
                            <input type="password" name="password" style={{boxShadow: "0px 0px 12px 0.8px " + shadowPassword}} placeholder="password" onChange={e => passwordValid(e.target.value)}/>
                        </div>
                        <div className="form-group">
                            <label htmlFor="email">email</label>
                            <br/>
                            <input type="email" name="email" style={{boxShadow: "0px 0px 12px 0.8px " + shadowEmail}} placeholder="email" onChange={e => emailValid(e.target.value)}/>
                        </div>
                        <div className="form-group">
                            <label htmlFor="phoneNumber">phone number</label>
                            <br/>
                            <input type="tel" name="phoneNumber" style={{boxShadow: "0px 0px 12px 0.8px " + shadowPhone}} placeholder="phone number" onChange={e => phoneValid(e.target.value)}/>
                        </div>
                    </div>
                </div>
                <div className="footer">
                    <button type="button" className="btn" onClick={e => beforeValid()}>
                        Register
                    </button>
                </div>
            </div>
        )
    
}