import React, { useState } from 'react';
import loginImg from "../../assets/Login.svg";
import "./style.scss";
import { useHistory } from "react-router-dom";


export function Login(){

    const [username,setUsername] = useState("1");
    const [password,setPassword] = useState("1");
    const [shadowUser,setShadowUser] = useState("transparent");
    const [shadowPassword,setShadowPassword] = useState("transparent");
    const history = useHistory();

    function usernameValid(value){

        setUsername(value)

        if(value == ""){
            setShadowUser("red")
            return false;
        }
        else if(value.length  < 4) {
            setShadowUser("red")
            return false;
        }
        else{
            setShadowUser("#0e81ce96")
            return true;
        }
    }

    function passwordValid(value){

        setPassword(value)

        if(value == ""){
            setShadowPassword("red")
            return false;
        }
        else if(value.length  < 4) {
            setShadowPassword("red")
            return false;
        }
        else{
            setShadowPassword("#0e81ce96")
            return true;
        }
    }

    function beforeValid(){
        var result;

        result = usernameValid(username);
        result = passwordValid(password) && result;

        if(result === true){
           
        }

        return result;
    }

        return(
            <div className="base-container">
                <div className="header">Login</div>
                <div className="content">
                    <div className="image">
                        <img src={loginImg}/>
                    </div>
                    <div className="form">
                        <div className="form-group">
                            <label htmlFor="username">Username</label>
                            <br/>
                            <input type="text" style={{boxShadow: "0px 0px 12px 0.8px " + shadowUser}} name="username" placeholder="username" onChange={e => usernameValid(e.target.value)}/>
                        </div>
                        <div className="form-group">
                            <label htmlFor="password">Password</label>
                            <br/>
                            <input type="password" style={{boxShadow: "0px 0px 12px 0.8px " + shadowPassword}} name="password" placeholder="password"  onChange={e => passwordValid(e.target.value)}/>
                        </div>
                    </div>
                </div>
                <div className="footer">
                    <button type="button" className="btn" onClick={e => beforeValid()} >
                        Login
                    </button>
                </div>
            </div>
        )
    
}