import React from 'react';
import "./stats.scss";
import { useState } from 'react';
import { faLock } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Container, Row, Col } from "react-bootstrap"



export function Stats() {

    const [digits, setDigits] = useState("");
    const [shadow, setShadow] = useState("transparent");
    const [opacity, setOpacity] = useState("80%");
    const [rotate, setRotate] = useState('0deg');

    function codeCheck(value) {

        setDigits(value)

        if (isNaN(value)) {
            setShadow("red")
            return false;
        }
        else if (!isNaN(value) && value.length == 4) {
            setDigits(value);
            setShadow("transparent")
            return true;
        }
        else if (value === "") {
            setShadow("transparent")
            return false;
        }
        else {
            setShadow("red")
            return false;
        }
    }

    function sleep(ms) {
        return new Promise(
          resolve => setTimeout(resolve, ms)
        );
    }
      

    function submitClick() {

        if(digits === ""){
            setOpacity("80%");
        }
        else if(codeCheck(digits) === false ){
            setOpacity("80%");
        }
        else{
            setOpacity("0%");
            setRotate('180deg');
        }
    }

    return (
        <Container className="crdit-card">
            <Row className="h-50">
                <Col className="mt-auto icon" style={{ opacity: opacity }} md={{ span: 3, offset: 5 }}><FontAwesomeIcon icon={faLock} size="6x" color="white" /></Col>
            </Row>
            <Row className="h-50">
                <Col className="mt-auto digits">
                    <input type="text" style={{ boxShadow: "0px 4px 16px 1px " + shadow }} id="secret-code" placeholder="****" onChange={e => codeCheck(e.target.value)}></input>
                </Col>
                <Col className="mt-auto submit">
                    <input type="submit" className="button" value="submit" onClick={e => submitClick()}></input>
                </Col>
            </Row>
        </Container>
    )
}